.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 1000; /* Ensure navbar is above other content */
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 60px 20px 20px; /* Adjust padding as needed */
  margin-top: 60px; /* Ensure content below navbar */
}

.search-bar {
  display: flex;
  align-items: center;
  width: 80%; /* Adjust width as needed */
  max-width: 600px; /* Limit maximum width */
}

.search-bar input[type='text'] {
  flex: 1;
  padding: 10px;
  border-radius: 5px;
  border: none;
  margin-right: 10px;
}

.search-bar button {
  padding: 10px 20px;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
}

.shopping-bag {
  cursor: pointer;
  margin-left: 10px; /* Add margin to the left */
}

.shopping-bag svg {
  width: 24px;
  height: 24px;
  fill: #000; /* Change fill color as needed */
}

.shopping-bag span {
  background-color: red;
  color: #fff;
  padding: 5px;
  border-radius: 50%;
  font-size: 12px;
}
.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.card {
  width: 300px;
  margin: 100px;
  padding: 100px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Add additional styling as needed */


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
